<template>
    <nav>
        <div class="nav-wrap">
            <div id="mobile" class="container">
                <router-link to="/" class="brand-logo center">Co dziś grają?</router-link>
                    <ul class="right">
                        <li v-if="isLoggedIn"><span class="email black-text">{{currentUser}}</span></li>
                        <li v-if="isLoggedIn"><router-link to='/film'>Repertuar</router-link></li>
                        <li v-if="isLoggedIn"><router-link to='/gps'>Najbliższe kino</router-link></li>
                        <li v-if="!isLoggedIn"><router-link to='/login'>Logowanie</router-link></li>
                        <li v-if="!isLoggedIn"><router-link to='/register'>Rejestracja</router-link></li>
                        <li v-if="isLoggedIn"><button v-on:click="logout" class="btn black">Wyloguj</button></li>
                    </ul>    
            </div>  
        </div>  
    </nav>
</template>

<script>
import firebase from 'firebase';

export default {
    name: 'navbar',
    data() {
        return {
            isLoggedIn: false,
            currentUser: false
        };
    },
  created() {
    if (firebase.auth().currentUser) {
      this.isLoggedIn = true;
      this.currentUser = firebase.auth().currentUser.email;
    }
  },
    methods: {
        logout: function() {
            firebase
            .auth()
            .signOut()
            .then(() => {
                this.$router.push('/login');
            });
            location.reload();
        }
    }
    
}
</script>

<style>
@media screen and (max-width: 600px) {
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  width: 60px;
} 

li{
  display: block;
  text-align: center;
}
}
</style>


